import React, { Component } from 'react'
import { Interpolate } from 'react-i18next'
import { Redirect, withRouter } from 'react-router-dom'
import cx from 'classnames'
import withTranslations from 'Shared/hoc/withTranslations'
import { PaymentStatuses, PaymentStatusesList } from 'Shared/constants/Payment'
import Button from 'Shared/components/Button'
import isUUID from 'Shared/helpers/isUUID'
import * as R from 'ramda'
import './PaymentStatus.scss'

class PaymentStatus extends Component {
  redirectToDashboard = () => this.props.history.push(this.props.redirectLink)

  contactTeam = () => <span><a href='mailto:team@oliv.com'>team@oliv.com</a></span>

  async componentDidMount () {
    const signature = R.path(['match', 'params', 'signature'], this.props)
    if (isUUID(signature)) {
      await this.props.finalizeProductTransaction(signature)
      this.props.fetchCurrentPlan()
      this.props.fetchUser()
    }
  }

  renderConfirmation = () => {
    const { t, match: { params: { status } } } = this.props

    const iconClass = cx({
      'payment-status__icon': true,
      'icon-im-success': status === PaymentStatuses.authorised,
      'icon-im-failure': status !== PaymentStatuses.authorised
    })

    return (
      <div className='payment-status__container parsing--hidden'>
        <div className='payment-status'>
          <div className='payment-status__wrapper'>
            <div className='payment-status__content'>
              <i className={iconClass} />
              <h2 className='payment-status__heading'>{t(`payment.confirmation.${status}.title`)}</h2>
              <Interpolate
                parent='p'
                i18nKey={`payment.confirmation.${status}.text`}
                className='payment-status__text'
                contactTeam={this.contactTeam()}
              />
              <Button onClick={this.redirectToDashboard}>{t(`payment.continue.${status}`)}</Button>
            </div>
          </div>
        </div>
      </div>
    )
  }

  render () {
    const { match: { params: { status } } } = this.props

    return PaymentStatusesList.includes(status)
      ? this.renderConfirmation()
      : <Redirect to={this.props.redirectLink} />
  }
}

export default withRouter(withTranslations(PaymentStatus))



