import { connect } from 'react-redux'
import PaymentStatus from 'Shared/components/PaymentStatus/PaymentStatus'
import { finalizeProductTransaction } from 'Shared/ducks/payment'
import { fetchEmployer } from 'Shared/ducks/user/employer'
import { fetchCurrentMembershipPlan } from 'Shared/ducks/membership'

const mapStateToProps = () => ({
  redirectLink: '/employer/job-offers/active'
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  finalizeProductTransaction: (transactionUUID) => dispatch(finalizeProductTransaction(transactionUUID)),
  fetchCurrentPlan: () => dispatch(fetchCurrentMembershipPlan()),
  fetchUser: () => dispatch(fetchEmployer(ownProps.match.params.id))
})

export default connect(mapStateToProps, mapDispatchToProps)(PaymentStatus)



